/**
 * HJChrome
 * JSONMock
 */
import React, { useState } from 'react';
import './App.less';
import ReactJson from 'react-json-view';
function App() {
  // JSON
  const [formatJsonInput, setFormatJsonInput] = useState('');
  // JSON
  const [formatJsonOutput, setFormatJsonOutput] = useState('');
  // Mock
  const [mockJsonInput, setMockJsonInput] = useState('');
  // Mock
  const [mockJsonOutput, setMockJsonOutput] = useState('');
  // 
  const [notification, setNotification] = useState({ message: '', type: '' });
  //  ('format'  'mock')
  const [activeTab, setActiveTab] = useState('format');


  /**
   * JSON
   * @description JSON
   * @returns {void}
   */
  const formatJSON = () => {
    try {
      if (!formatJsonInput) {
        showNotification('JSON', 'warning');
        return;
      }
      const json = JSON.parse(formatJsonInput);
      setFormatJsonOutput(JSON.stringify(json, null, 2));
      showNotification('', 'success');
    } catch (error) {
      setFormatJsonOutput(null);
      showNotification('JSON: ' + error.message, 'error');
    }
  };

  /**
   * 
   * @description Mock
   * @returns {void}
   */
  const copyToClipboard = () => {
    const textToCopy = activeTab === 'format' ? formatJsonOutput : mockJsonOutput;
    navigator.clipboard.writeText(textToCopy).then(() => {
      showNotification('', 'success');
    }).catch(() => {
      showNotification('', 'error');
    });
  };

  /**
   * Mock
   * @description JSONMock
   * @returns {void}
   */
  const generateMockFromResult = () => {
    try {
      if (!mockJsonInput) {
        showNotification('JSON', 'warning');
        return;
      }

      let data;
      try {
        // JSON
        data = JSON.parse(mockJsonInput);
      } catch (error) {
        setMockJsonOutput(null);
        showNotification('JSON: ' + error.message, 'error');
        return;
      }

      // Mock
      const mockData = {
        data: data,  // dataJSON
        statusCode: 200,
        header: 'Content-Type: application/json'
      };

      // MockJSON
      setMockJsonOutput(JSON.stringify(mockData, null, 2));
      showNotification('Mock', 'success');
    } catch (error) {
      setMockJsonOutput(null);
      showNotification(': ' + error.message, 'error');
    }
  };

  /**
   * 
   * @description 3
   * @param {string} message - 
   * @param {string} type -  ('success', 'error', 'warning')
   * @returns {void}
   */
  const showNotification = (message, type = 'success') => {
    setNotification({ message, type });
    setTimeout(() => {
      setNotification({ message: '', type: '' });
    }, 3000);
  };

  return (
    <div className="body-class">
      <div className="container">
        <h1 className="title-h1">HJChrome</h1>
        <p className="paragraph">JSONmock</p>

      {/* Tab */}
      <div className="tabs">
        <button 
          className={`tab-button ${activeTab === 'format' ? 'active' : ''}`}
          onClick={() => {
            setActiveTab('format');
            setMockJsonInput('');
            setMockJsonOutput('');
          }}
        >
          JSON
        </button>
        <button 
          className={`tab-button ${activeTab === 'mock' ? 'active' : ''}`}
          onClick={() => {
            setActiveTab('mock');
            setFormatJsonInput('');
            setFormatJsonOutput('');
          }}
        >
          Mock
        </button>
      </div>

      {/*  */}
      <div className="tab-content">
        {activeTab === 'format' && (
          <div className="section">
            <h2>JSON</h2>
            <textarea
              className="textarea-class"
              value={formatJsonInput}
              onChange={(e) => setFormatJsonInput(e.target.value)}
              placeholder="JSON..."
            ></textarea>
            <div className="buttons">
              <button className="button-class" onClick={formatJSON}></button>
              <button className="button-class copy-btn" onClick={copyToClipboard}></button>
            </div>
            {formatJsonOutput && (
              <div className="json-view-container">
                <h3>:</h3>
                {(() => {
                  try {
                    const jsonData = JSON.parse(formatJsonOutput);
                    return <ReactJson src={jsonData} theme="monokai" />;
                  } catch (error) {
                    return <div className="error-message">: {error.message}</div>;
                  }
                })()}
              </div>
            )}
          </div>
        )}

        {activeTab === 'mock' && (
          <div className="section">
            <h2>Mock</h2>
            <textarea
              className="textarea-class"
              value={mockJsonInput}
              onChange={(e) => setMockJsonInput(e.target.value)}
              placeholder="JSON"
            ></textarea>
            <div className="buttons">
              <button className="button-class mock-btn" onClick={generateMockFromResult}>Mock</button>
              <button className="button-class copy-btn" onClick={copyToClipboard}></button>
            </div>
            {mockJsonOutput && (
              <div className="json-view-container">
                <h3>Mock:</h3>
                {(() => {
                  try {
                    const jsonData = JSON.parse(mockJsonOutput);
                    return <ReactJson src={jsonData} theme="monokai" />;
                  } catch (error) {
                    return <div className="error-message">: {error.message}</div>;
                  }
                })()}
              </div>
            )}
          </div>
        )}
      </div>

      {/*  */}
      {notification.message && (
        <div className={`notification ${notification.type}`}>
          {notification.message}
        </div>
      )}
    </div>
  </div>
  );
}

export default App;